* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(90deg, #69b7eb, #b3dbd3, #f4d6db);

    .leader-wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 400px;
        border: 1px solid black;
        border-top: none;
        border-radius: 10px;
        transition: all 0.3s;
        background-color: black;
        color: white;
    }
}

header {
    min-height: 75px;
    width: 450px;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #edf6f9;
    border-radius: 10px;

    .moves {
        width: 26%;
        font: normal normal 18px/110% 'Goldman', sans-serif;
    }

    .time {
        width: 15%;

        .stop-watch {
            font: normal normal 18px/110% 'Goldman', sans-serif;
        }
    }

    .volume-up {
        color: black;
        cursor: pointer;
        transition: 0.4s;

        &:hover {
            transform: scale(1.5);
        }
    }

    .pause-button {
        border: none;
        outline: none;
        width: 70px;
        height: 20px;
        background: none;
        transition: 0.4s;
        font: normal normal 18px/110% 'Goldman', sans-serif;

        &:hover {
            cursor: pointer;
        }
    }

    .reload {
        color: black;

        transition: 0.4s;

        &:hover {
            transform: scale(1.5);
            cursor: pointer;
        }
    }
}

.field {
    position: relative;
    width: 450px;
    height: 450px;
    margin: 0 auto;
    background-color: white;
    border-radius: 10px;
    background: rgba(179, 219, 211, 1);

    .overlay {
        position: absolute;
        z-index: 99;
        background: rgba(0, 0, 0, .8);

        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        border-radius: 10px;

        .win {
            color: white;

        }

        .new-game {

            border: none;
            outline: none;
            width: 111px;
            height: 20px;
            background: none;
            transition: 0.4s;
            font: normal normal 22px/110% 'Goldman', sans-serif;
            color: white;
            text-transform: uppercase;

            &:hover {
                cursor: pointer;
                color: gray;
            }
        }

        .save-game {
            border: none;
            outline: none;
            width: 111px;
            height: 20px;
            background: none;
            transition: 0.4s;
            font: normal normal 22px/110% 'Goldman', sans-serif;
            color: white;
            text-transform: uppercase;

            &:hover {
                cursor: pointer;
                color: gray;
            }
        }

        .load-game {
            border: none;
            outline: none;
            width: 111px;
            height: 20px;
            background: none;
            transition: 0.4s;
            font: normal normal 22px/110% 'Goldman', sans-serif;
            color: white;
            text-transform: uppercase;

            &:hover {
                cursor: pointer;
                color: gray;
            }
        }

        .leader-button {
            border: none;
            outline: none;
            width: 111px;
            height: 20px;
            background: none;
            transition: 0.4s;
            font: normal normal 22px/110% 'Goldman', sans-serif;
            color: white;
            text-transform: uppercase;

            &:hover {
                cursor: pointer;
                color: gray;
            }
        }

        .choose-size {
            width: 100px;
            margin: 0 auto;
        }

        .save-change {
            position: absolute;
            bottom: 0;
            font: normal normal 15px/110% 'Goldman', sans-serif;
            color: whitesmoke;
            transition: 0.5s;
        }
    }

    &__item {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100px;
        height: 100px;
        padding: 5px;
        border-radius: 8px;
        background-color: #D1DEDE;
        font-size: 30px;
        transition: all 0.5s;
        color: #fff0;
        font: normal normal 22px/110% 'Goldman', sans-serif;
        filter: grayscale(0.5);

    }
}

.empty {
    background-color: white;
}

.hidden {
    visibility: hidden;
}